import { BaseButton } from "./baseButton";
import { levelSelectInfo } from "./levelSelectInfo";

export class ArrowButton extends BaseButton
{
    private onPress: Phaser.Signal = new Phaser.Signal();

    constructor(game: Phaser.Game, x: number, y: number, pressCallback: Function, context: any)
    {
        super(game, x, y, levelSelectInfo.buttonArrowSprite.key);

        this.onPress.add(pressCallback, context);
    }

    public OnButtonPress(): void
    {
        this.onPress.dispatch();
    }
}



