import { GameObject } from "./gameObject";
import * as MusicHandler from "./musicHandler";
import { levelSelectInfo } from "./levelSelectInfo";

export class MuteButton extends GameObject
{
    private onSprite: Phaser.Sprite;
    private offSprite: Phaser.Sprite;

    constructor(game: Phaser.Game, x: number, y: number)
    {
        super(game, x, y, "", 0);

        this.onSprite = this.game.add.sprite(x, y, levelSelectInfo.speakerOnSprite.key);
        this.onSprite.anchor = new Phaser.Point(0.5, 0.5);
        this.onSprite.inputEnabled = true;
        this.onSprite.events.onInputOver.add(this.InputOver, this);
        this.onSprite.events.onInputOut.add(this.InputOut, this);
        this.onSprite.events.onInputDown.add(this.InputDown, this);

        this.offSprite = this.game.add.sprite(x, y, levelSelectInfo.speakerOffSprite.key);
        this.offSprite.anchor = new Phaser.Point(0.5, 0.5);
        this.offSprite.inputEnabled = true;
        this.offSprite.events.onInputOver.add(this.InputOver, this);
        this.offSprite.events.onInputOut.add(this.InputOut, this);
        this.offSprite.events.onInputDown.add(this.InputDown, this);

        this.onSprite.visible = !MusicHandler.IsMuted();
        this.offSprite.visible = MusicHandler.IsMuted();
    }

    public InputOver(): void
    {
        this.offSprite.tint = 0xd3d3d3;
        this.onSprite.tint = 0xd3d3d3;
    }

    public InputOut(): void
    {
        this.offSprite.tint = 0xFFFFFF;
        this.onSprite.tint = 0xFFFFFF;
    }

    public InputDown(): void
    {
        MusicHandler.ToggleMute(!MusicHandler.IsMuted());
        this.UpdateButtonSprite();
    }

    public UpdateButtonSprite(): void
    {
        this.onSprite.visible = !MusicHandler.IsMuted();
        this.offSprite.visible = MusicHandler.IsMuted();
    }

    public Update(): void
    {

    }
}


