import * as Libs from "Libs/";
import * as MainStateEvents from "States/Events/";
import { levelSelectInfo } from "./levelSelectInfo";
import { defineRequire } from "outsideRequire";


let saveGameDictionary: ISaveGameDictionary = Object(null);

export function getLevelData(): ILevelData[] | undefined
{
    if (saveGameDictionary[levelSelectInfo.gameBeginPlayed])
        return saveGameDictionary[levelSelectInfo.gameBeginPlayed].levels;
}

export function setLevelData(levelIndex: number, data: ILevelData)
{
    if (saveGameDictionary[levelSelectInfo.gameBeginPlayed])
        loadGame();

    saveGameDictionary[levelSelectInfo.gameBeginPlayed].
        levels[levelIndex] = data;
}

export function saveGame()
{
    if (saveGameDictionary[levelSelectInfo.gameBeginPlayed])
    {
        localStorage.setItem(levelSelectInfo.gameBeginPlayed + "-SaveData",
            JSON.stringify(saveGameDictionary[levelSelectInfo.gameBeginPlayed]));
    }
}

export function loadGame()
{
    let data = localStorage.getItem(levelSelectInfo.gameBeginPlayed + "-SaveData");

    if (data)
        saveGameDictionary[levelSelectInfo.gameBeginPlayed] = JSON.parse(data);
    else
        saveGameDictionary[levelSelectInfo.gameBeginPlayed] = makeFreshSaveData();
}

function makeFreshSaveData(): ISaveData
{
    let returnValue: ISaveData = { levels: new Array(levelSelectInfo.amountOfLevels) };
    for (let index = 0; index < returnValue.levels.length; index++)
    {
        returnValue.levels[index] = { completed: false };
    }
    return returnValue;
}

export function hasDataLoaded(saveGame: GameTypes): boolean
{
    return saveGameDictionary[saveGame] ? true : false;
}

MainStateEvents.events.onLevelWon.add((state) =>
{
    if (window.__Game__DEBUG)
        console.log(`level ${state.level + 1} won`);

    setLevelData(state.level, { completed: true });
    saveGame();
});





export interface ISaveGameDictionary
{
    [value: string]: ISaveData;
}


export interface ISaveData
{
    levels: ILevelData[];
}

export interface ILevelData
{
    completed: boolean;
}


defineRequire("states/LevelSelect/saveHandler", this);



