import { game } from "main";
import { levelSelectInfo } from "./levelSelectInfo";


export function LoadLevel(index: number)
{
    game.load.baseURL = "";
    game.state.start("main", undefined, undefined, index, levelSelectInfo.gameBeginPlayed);
}

export function GoToMainMenu()
{
    game.load.baseURL = "";
    game.state.start("gameSelect");
}

export function GoToLevelSelect()
{
    game.load.baseURL = "";
    game.state.start("levelSelect");
}



