import { game } from "main";
import { assetLoadMethod, StateEnum } from "assetsLoader";

export class BasketsObject extends Phaser.Sprite
{

    public static readonly doodadName = "Baskets";


    @assetLoadMethod(StateEnum.Preload, "world2/baskets")
    private static _loadAssets(game: Phaser.Game)
    {
        game.load.image("baskets", "images/mathman/World2/baskets.png");
    }

    constructor(game: Phaser.Game, x: number, y: number)
    {
        super(game, x, y, "baskets");
        this.anchor.set(0.5, 0.5);
    }
}


