import { game } from "main";

let audioObj: Phaser.Sound;
let isMuted: boolean | undefined;

export function AddMusic(audioKey: string, volume: number, loop: boolean): void
{
    if (isMuted === undefined)
        isMuted = (localStorage.getItem("musicMuted") === "true") ? true : false;

    this.audioObj = game.add.audio(audioKey, volume, loop);
    this.audioObj.mute = isMuted;
}

export function IsMusicLooping(): boolean
{
    if (this.audioObj === undefined || this.audioObj == null)
        return false;

    return this.audioObj.loop;
}

export function IsMusicPlaying(): boolean
{
    if (this.audioObj === undefined || this.audioObj == null)
        return false;

    return this.audioObj.isPlaying;
}

export function PlayMusic(): void
{
    if (this.audioObj === undefined || this.audioObj == null)
    {
        return;
    }

    this.audioObj.play();
}

export function StopMusic(): void
{
    if (this.audioObj === undefined || this.audioObj == null)
    {
        return;
    }

    this.audioObj.stop();
}

export function ToggleMute(toggle: boolean): void
{
    isMuted = toggle;
    if (this.audioObj === undefined)
    {
        return;
    }

    this.audioObj.mute = isMuted;
    this.SaveMute(toggle);
}

function SaveMute(toggle: boolean): void
{
    localStorage.setItem("musicMuted", toggle ? "true" : "false");
}

export function IsMuted(): boolean
{
    if (isMuted === undefined)
    {
        return false;
    }

    return isMuted;
}




