import { ArrowButton } from "./arrowButton";
import { MuteButton } from "./muteButton";
import { BackButtonGameSelect } from "./backButtonGameSelect";
import { levelSelectInfo } from "./levelSelectInfo";

export function CreateBackButtonGameSelect(game: Phaser.Game, x: number, y: number, goToLevelSelect?: boolean)
{
    let tempBackButton = new BackButtonGameSelect(game, x, y, levelSelectInfo.backButtonSprite.key, goToLevelSelect);

    return tempBackButton;
}

export function CreateMuteButton(game: Phaser.Game, x: number, y: number)
{
    let tempBackButton = new MuteButton(game, x, y);

    return tempBackButton;
}

export function CreateArrowButton(game: Phaser.Game, x: number, y: number, pressCallback: Function, context: any)
{
    let tempArrowButton = new ArrowButton(game, x, y, pressCallback, context);

    return tempArrowButton;
}


