import { GameObject } from "./gameObject";


export abstract class BaseButton extends GameObject
{
    private intractable: boolean = true;
    protected intractableResetTime: number = 150;
    protected hoverOverTint: number = 0xd3d3d3;
    protected hoverOutTint: number = 0xFFFFFF;

    private previousTween: Phaser.Tween;
    private currentTween: Phaser.Tween;

    constructor(game: Phaser.Game, x: number, y: number, key: string)
    {
        super(game, x, y, key, 0);

        this.anchor = new Phaser.Point(0.5, 0.5);

        this.inputEnabled = true;

        this.events.onInputOver.add(this.InputOver, this);
        this.events.onInputOut.add(this.InputOut, this);
        this.events.onInputDown.add(this.InputDown, this);
    }

    public InputOver(): void
    {
        this.tint = this.hoverOverTint;
    }

    public InputOut(): void
    {
        this.tint = this.hoverOutTint;
    }

    public InputDown(): void
    {
        if (this.intractable === true && this.visible === true)
        {
            this.SetUnIntractable();

            this.game.time.events.add(150, this.SetIntractable, this);

            this.OnButtonPress();
        }
    }

    public Fade(fadeIn: boolean, instantTransition: boolean): void
    {
        if (this.previousTween !== null && this.previousTween !== undefined)
        {
            this.previousTween.stop();
        }

        if (!instantTransition)
        {
            let startAlpha: number = (fadeIn === true) ? 0 : 1;
            let targetAlpha: number = (fadeIn === true) ? 1 : 0;

            this.alpha = startAlpha;

            this.currentTween = this.game.add.tween(this).to({ alpha: targetAlpha }, 350, Phaser.Easing.Linear.None);
            this.previousTween = this.currentTween;

            if (fadeIn)
            {
                this.SetVisible();
            }
            else
            {
                this.currentTween.onComplete.addOnce(this.SetInvisible, this);
            }

            this.currentTween.start();
        }
        else
        {
            if (fadeIn)
            {
                this.SetVisible();
                this.alpha = 1;
            }
            else
            {
                this.SetInvisible();
                this.alpha = 0;
            }
        }

    }

    private SetVisible(): void
    {
        this.visible = true;
    }

    private SetInvisible(): void
    {
        this.visible = false;
    }

    private SetUnIntractable(): void
    {
        this.intractable = false;
    }

    private SetIntractable(): void
    {
        this.intractable = true;
    }

    public Update()
    {

    }

    public abstract OnButtonPress(): void;


}



