import { BaseButton } from "./baseButton";
import { LevelSelectState } from "./levelSelectState";
import { GoToMainMenu, GoToLevelSelect } from "./levelHandler";


export class BackButtonGameSelect extends BaseButton
{
    constructor(game: Phaser.Game, x: number, y: number, key: string, private goToLevelSelect?: boolean)
    {
        super(game, x, y, key);
    }

    public OnButtonPress()
    {
        if (this.goToLevelSelect)
        {
            GoToLevelSelect();
        }
        else
        {
            GoToMainMenu();
        }
    }


    public Update()
    {

    }
}



