import * as Libs from "Libs/";

export type UrlAndKey = { key: string, spriteUrl: string };


class LevelSelectInfo
{
    public gameBeginPlayed: GameTypes;
    public amountOfLevels: number;
    public backButtonSprite: UrlAndKey;
    public loadScreenSprite: UrlAndKey;
    public levelSelectBackgroundSprite: UrlAndKey;
    public buttonBackgroundSprites: UrlAndKey[];
    public buttonBackgroundGreySprites: UrlAndKey[];
    public buttonArrowSprite: UrlAndKey;
    public speakerOnSprite: UrlAndKey;
    public speakerOffSprite: UrlAndKey;
    public flavorTextTextStyle: Phaser.PhaserTextStyle;
    public flavorTextYOffset: number;
    public flavorTextPerPage: string[];
    public flavorTextFadeInSpeed: number;

    public setGameType(newGameType: GameTypes)
    {
        this.gameBeginPlayed = newGameType;
        if (newGameType === "equation")
        {
            this.amountOfLevels = 50;
            this.backButtonSprite = { key: "backButtonKey", spriteUrl: "images/backButton.png" };
            this.loadScreenSprite = { key: "loadScreenKey", spriteUrl: "images/percentageLoadScreen.jpg" };
            this.levelSelectBackgroundSprite = { key: "levelSelectBackgroundKey", spriteUrl: "images/lvlbg.png" };

            this.buttonBackgroundSprites = [
                { key: "buttonBackgroundKeyEquation1", spriteUrl: "images/snip41.png" },
                { key: "buttonBackgroundKeyEquation2", spriteUrl: "images/snip42.png" },
                { key: "buttonBackgroundKeyEquation3", spriteUrl: "images/snip43.png" },
                { key: "buttonBackgroundKeyEquation4", spriteUrl: "images/snip44.png" }
            ];

            this.buttonBackgroundGreySprites = [
                { key: "buttonBackgroundGreyKeyEquation1", spriteUrl: "images/snip41 copy.png" },
                { key: "buttonBackgroundGreyKeyEquation2", spriteUrl: "images/snip42 copy.png" },
                { key: "buttonBackgroundGreyKeyEquation3", spriteUrl: "images/snip43 copy.png" },
                { key: "buttonBackgroundGreyKeyEquation4", spriteUrl: "images/snip44 copy.png" }
            ];

            this.buttonArrowSprite = { key: "buttonArrowKey", spriteUrl: "images/btnArrowTest.png" };
            this.speakerOnSprite = { key: "speakerOnKey", spriteUrl: "images/speakerOn.png" };
            this.speakerOffSprite = { key: "speakerOffKey", spriteUrl: "images/speakerOff.png" };
            this.flavorTextTextStyle = {
                font: 46 + "px Amaranth",
                fill: "#FFFFFF",
                fontStyle: "bold"
            };
            this.flavorTextFadeInSpeed = 450;
            this.flavorTextYOffset = 75;
            this.flavorTextPerPage = [
                "",
                "",
                "",
                "",
                "",
                ""
            ];
        }
        else
        {
            this.amountOfLevels = 40;
            this.backButtonSprite = { key: "backButtonKey", spriteUrl: "images/backButton.png" };
            this.loadScreenSprite = { key: "loadScreenKey", spriteUrl: "images/percentageLoadScreen.jpg" };
            this.levelSelectBackgroundSprite = { key: "levelSelectBackgroundKey", spriteUrl: "images/lvlbg.png" };
            this.buttonBackgroundSprites = [
                { key: "buttonBackgroundKeyOrder1", spriteUrl: "images/snip11.png" },
                { key: "buttonBackgroundKeyOrder2", spriteUrl: "images/snip12.png" },
                { key: "buttonBackgroundKeyOrder3", spriteUrl: "images/snip13.png" },
                { key: "buttonBackgroundKeyOrder4", spriteUrl: "images/snip14.png" }
            ];
            this.buttonBackgroundGreySprites = [
                { key: "buttonBackgroundGreyKeyOrder1", spriteUrl: "images/snip11 copy.png" },
                { key: "buttonBackgroundGreyKeyOrder2", spriteUrl: "images/snip12 copy.png" },
                { key: "buttonBackgroundGreyKeyOrder3", spriteUrl: "images/snip13 copy.png" },
                { key: "buttonBackgroundGreyKeyOrder4", spriteUrl: "images/snip14 copy.png" }
            ];
            this.buttonArrowSprite = { key: "buttonArrowKey", spriteUrl: "images/btnArrowTest.png" };
            this.speakerOnSprite = { key: "speakerOnKey", spriteUrl: "images/speakerOn.png" };
            this.speakerOffSprite = { key: "speakerOffKey", spriteUrl: "images/speakerOff.png" };
            this.flavorTextTextStyle = {
                font: 46 + "px Amaranth",
                fill: "#FFFFFF",
                fontStyle: "bold"
            };
            this.flavorTextFadeInSpeed = 450;
            this.flavorTextYOffset = 75;
            this.flavorTextPerPage = [
                "",
                "",
                "",
                "",
                "",
                ""
            ];
        }
    }

    constructor(gameBeginPlayed: GameTypes)
    {
        this.setGameType(gameBeginPlayed);
    }
}



export let levelSelectInfo = new LevelSelectInfo("order");


